import React, {Component} from 'react';
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';
import {SortableContainer, SortableElement} from 'react-sortable-hoc';
import arrayMove from 'array-move';

class CategoryImage extends Component{
    constructor(props){
        super(props);
        this.state = {
            images: [],
            id : this.props.idcategory
        }
    }

    onSortEnd = ({oldIndex, newIndex}) => {
        this.setState(({images}) => ({
          images: arrayMove(images, oldIndex, newIndex),
        }), () => {
            const payload = {
                "fileUpdate" : this.state.images,
                "id": this.state.id
            }
            let token= reactLocalStorage.get('auth');
            const AuthStr = "Bearer ".concat(token); 
            axios.put('http://13.229.249.101/dashboard/api/category/upload/edit', payload, { headers: { Authorization: AuthStr } })
            .then((response) => {
                
            })
            .catch((error) => {
                console.log(error);
            });  
        });
      };
    componentDidUpdate(prevProps){
        if(prevProps.image !== this.props.image){
            this.setState({
                images : this.props.image,
                id : this.props.idcategory
            });
        }      
     }

     remove = (index) => {
        const images = this.state.images;
        images.splice(index, 1);
        this.setState({
            images : images
        }, () => {
            const payload = {
                "fileUpdate" : this.state.images,
                "id": this.state.id
            }
            let token= reactLocalStorage.get('auth');
            const AuthStr = "Bearer ".concat(token); 
            axios.put('http://13.229.249.101/dashboard/api/category/upload/edit', payload, { headers: { Authorization: AuthStr } })
            .then((response) => {
                window.location.href='#/admin/categoryedit/'+this.state.id;
            })
            .catch((error) => {
                console.log(error);
            });  
        });
       
    }
    
    render(){
        const SortableItem = SortableElement(({value}) => <div className="list-group-item"><img alt="" src={"http://13.229.249.101/dashboard/api/web/uploads/category/"+value} className="w-100"></img> </div>);

        const SortableList = SortableContainer(({images}) => {
        return (
            <div className="row">
            {images.map((value, index) => (
                <div key={`item-${index}`} className="col-md-2 col-4 p-2">
                <SortableItem index={index} value={value}  />
                <a href="javascript:void(0)" className="float-right position-absolute mt-0 close-btn" onClick={() => this.remove(index)}><i className="nav-icon material-icons">close</i></a>
                </div>
            ))}
            </div>
        );
        });

        return(
            <div className="box-default m-3">
                <div className="box-body py-5">
                    <div className="form-group">
                        <h6>Image Category</h6>
                    </div>
                    <div className="form-group">
                        <div className="col-md-12">
                            
                            <SortableList images={this.state.images} onSortEnd={this.onSortEnd} axis="xy"  />
                           
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default CategoryImage;