import React, {Component} from 'react';
import axios from 'axios';
import FileBase64 from 'react-file-base64';
import {reactLocalStorage} from 'reactjs-localstorage';
import SweetAlert from 'sweetalert2-react';
import $ from 'jquery';
import './../../category/Category.css';

class CategoryColor extends Component{
    constructor(props){
        super(props);
        this.state = {
            id : this.props.idcategory,
            color : [],
            file : [],
            tempcolor : ""
        }
        this.loadHandler = this.loadHandler.bind(this);
        this.loadHandler();
    }

    loadHandler = () => {
        var headers = {
            'Content-Type': 'application/json'
        }
        axios.get("http://13.229.249.101/dashboard/api/userapi/product/view/webnew/category/"+this.props.idcategory, {headers: headers})
        .then(respons => {
            var colors = [];
            var json = respons.data;
            Object.keys(json).map((warna)=>{
                colors.push(warna)
            });
            this.setState({"color" : colors});
        });
    }

    submitHandler = color=> event => {
        if(this.state.file !== '' && this.state.file !== undefined && this.state.file !== null){
        const payload = {
            "file" : this.state.file,
            "warna": color
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/category/upload/color/new', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            if(response.status === 200){
                this.loadHandler();
                this.setState({
                    tempcolor : this.state.file,
                    file : ""
                });
                $('.'+color).find('img').attr('src', this.state.tempcolor);
            }
        })
        .catch((error) => {
            console.log(error);
        });  
        }   
        event.preventDefault(); 
    }

    submitHandlerColor = color=> event => {
        if(this.state.file !== '' && this.state.file !== undefined && this.state.file !== null){
        const payload = {
            "file" : this.state.file,
            "warna": color
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/category/upload/productcolor/new', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            if(response.status === 200){
                this.loadHandler();
                this.setState({
                    tempcolor : this.state.file,
                    file : ""
                });
                $('.'+color).find('.product-color').attr('src', this.state.tempcolor);
            }
        })
        .catch((error) => {
            console.log(error);
        });  
        }   
        event.preventDefault(); 
    }

    componentDidUpdate(prevProps){
        if((prevProps.idcategory !== this.props.idcategory) || prevProps.idcategory === undefined){
           this.setState({
               id : this.props.idcategory
           });
           this.loadHandler();
         
        }   
 
     }
    getFiles(files){
        if(files.type === "image/jpeg"){
            var image = files.base64;
            var img = document.createElement("img")
            img.setAttribute("src", image)
            setTimeout(
                function() {
                    if(img.height === 200 && img.width === 200){
                        this.setState({ "file" :  files.base64 });
                    }
                    else{
                        this.setState({
                            show: true, 
                            titlealert: "Image Size Failed", 
                            textalert : "Image Size 200px X 200px"
                        })
                        $("input[type='file']").val("");
                    }
                }
                .bind(this),
                0
            );
        }
        else{
            this.setState({
                show: true, 
                titlealert: "Image Type Failed", 
                textalert : "Image Type Must .Jpg"
            });
        }
    }

    getFilesProduct(files){
        if(files.type === "image/jpeg"){
            var image = files.base64;
            var img = document.createElement("img")
            img.setAttribute("src", image)
            setTimeout(
                function() {
                    if(img.height === 458 && img.width === 458){
                        this.setState({ "file" :  files.base64 });
                    }
                    else{
                        this.setState({
                            show: true, 
                            titlealert: "Image Size Failed", 
                            textalert : "Image Size 458px X 458px"
                        })
                        $("input[type='file']").val("");
                    }
                }
                .bind(this),
                0
            );
        }
        else{
            this.setState({
                show: true, 
                titlealert: "Image Type Failed", 
                textalert : "Image Type Must .Jpg"
            });
        }
    }

    render (){
        const id = this.state.id;
        return (
            <div>
            <SweetAlert
                show = {this.state.show}
                title = {this.state.titlealert}
                text = {this.state.textalert}
                onConfirm={() => 
                    this.setState({ show: false, titlealert: "", textalert : "" })
                }
            />
            <div className="box-default m-3">
                <div className="box-body py-5">
                    <div className="form-group">
                        <h6>Category Color</h6>
                    </div>
                    
                    <div className="form-group mx-0 row">
                    {
                        this.state.color.map((color,i) => {
                            const warna = id+'-'+color.split(' ').join('');
                            return <div className="col-md-2 p-2 mb-3 " key={i}>
                                <div className="border p-2 text-center">
                                    <form  onSubmit={this.submitHandler(warna)} className={warna}>
                                    <img src={"http://13.229.249.101/dashboard/api/web/uploads/category/color/"+warna+".jpg"}  className="mb-2 w-100" />
                                    <FileBase64
                                    multiple={ false }
                                    onDone={ this.getFiles.bind(this) }
                                    className="w-100"
                                    />
                                    <p><strong>{color}</strong></p>
                                    <button className="btn btn-outline-info mt-2" type="submit"> Upload </button>
                                    </form>
                                </div>

                                <div className="border p-2 text-center">
                                    <form  onSubmit={this.submitHandlerColor(warna)} className={warna}>
                                    <img src={"hhttp://13.229.249.101/dashboard/api/web/uploads/category/productcolor/"+warna+".jpg"}  className="mb-2 w-100 product-color" />
                                    <FileBase64
                                    multiple={ false }
                                    onDone={ this.getFilesProduct.bind(this) }
                                    className="w-100"
                                    />
                                    <p><strong>{color}</strong></p>
                                    <button className="btn btn-outline-info mt-2" type="submit"> Upload </button>
                                    </form>
                                </div>
                            </div>
                        })
                    }
                    </div>                   
                </div>
            </div>
            </div>
        )
    }
}

export default CategoryColor; 

