import React,{Component} from 'react';
import axios from 'axios';
import Button from '@material-ui/core/Button';
import FileBase64 from 'react-file-base64';
import {reactLocalStorage} from 'reactjs-localstorage';
import { loadProgressBar } from 'axios-progress-bar';
import CategoryImage from './CategoryImage';
import SweetAlert from 'sweetalert2-react';
import $ from 'jquery';

class CategoryUpload extends Component{
    constructor(props){
        super(props);
        this.state = {
            files: "",
            id : this.props.idcategory,
            categoryid : this.props.idcategory,
            imageweb : [],
            textalert : "",
            titlealert : "",
            show : false
        }
        this.categoryHandle = this.categoryHandle.bind(this);
        this.categoryHandle();
    }

    getFiles(files){
        if(files.type === "image/jpeg"){
            var image = files.base64;
            var img = document.createElement("img")
            img.setAttribute("src", image)
            setTimeout(
                function() {
                    if(img.height === 458 && img.width === 458){
                        this.setState({ files: files.base64 });
                    }
                    else{
                        this.setState({
                            show: true, 
                            titlealert: "Image Size Failed", 
                            textalert : "Image Size 458px X 458px"
                        })
                        $("input[type='file']").val("");
                    }
                }
                .bind(this),
                0
            );
        }
        else{
            this.setState({
                show: true, 
                titlealert: "Image Type Failed", 
                textalert : "Image Type Must .Jpg"
            });
        }
    }
    submitHandler = (event) => {
        if(this.state.files !== '' && this.state.files !== undefined && this.state.files !== null){
        loadProgressBar();
        const payload = {
            "file" : this.state.files,
            "id": this.state.id
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/category/upload/new', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            if(response.statusText === 'OK'){
                this.categoryHandle();
            }
        })
        .catch((error) => {
            console.log(error);
        });  
        }   
        event.preventDefault(); 
    }

    categoryHandle = () => {
        let id = this.props.idcategory;
        let token= reactLocalStorage.get('auth');
        let AuthStr = "Bearer ".concat(token);
        axios.get('http://13.229.249.101/dashboard/api/category/view/'+id, { headers: { Authorization: AuthStr } })
        .then(response => {
            const datas = response.data;
            this.setState({
                brand : datas.brandId,
                group : datas.groupQty,
                name : datas.name,
                description : datas.description,
                color : datas.color,
                cartoon : datas.cartoon,
                pack : datas.pack,
                mill : datas.mill,
                class : datas.class,
                descriptionweb : datas.descriptionWeb,
                tagweb : datas.tagWeb,
                priceweb : datas.priceWeb,
                imageweb : datas.imageWeb
            })
        })
        .catch((error) => {
            console.log('error ' + error);
        });
    }

    componentDidUpdate(prevProps){
        if(prevProps.idcategory !== this.props.idcategory){

           this.setState({
               id : this.props.idcategory
           });
           this.categoryHandle();
        }      
     }
    render(){
        const id = this.state.categoryid;
        const image = this.state.imageweb;
        return(
            <div>
            <SweetAlert
                show = {this.state.show}
                title = {this.state.titlealert}
                text = {this.state.textalert}
                onConfirm={() => 
                    this.setState({ show: false, titlealert: "", textalert : "" })
                }
            />
            <div className="box-default m-3">
                <div className="box-body py-5">
                    <div className="form-group">
                        <h6>Upload Image Category</h6>
                    </div>
                    <form  onSubmit={this.submitHandler}>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Upload Image</label>
                        <div className="col-md-10">
                        <FileBase64
                        multiple={ false }
                        onDone={ this.getFiles.bind(this) }
                        />
                        <br/>
                        <small>Image Size 458px X 458px</small>
                        </div>
                    </div>
                    <div className="form-group row">
                        <div className="col-md-2"></div>
                        <div className="col-md-10">
                        <Button variant="contained" className="btn-w-md" type="submit"> Upload </Button>
                        </div>
                    </div>
                    </form>
                </div>
            </div>
            {
                image !== undefined ?
                <CategoryImage idcategory={id} image={image} />
                :null
            }
            </div>
        );
    }
}

export default CategoryUpload; 