import React,{Component} from 'react';
import {Redirect} from 'react-router-dom';
import axios from 'axios';
import PropTypes from 'prop-types';
import { Editor } from '@tinymce/tinymce-react';
import {reactLocalStorage} from 'reactjs-localstorage';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import CategoryUpload from './CategoryUpload';
import CategoryColor from './CategoryColor';
import Switch from '@material-ui/core/Switch';
 
const styles = theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    formControl: {
      margin: theme.spacing.unit,
      minWidth: 150,
    },
    selectEmpty: {
      marginTop: theme.spacing.unit * 0,
    },
    group: {
        margin: `${theme.spacing.unit}px 0`,
        marginTop: '0px'
      },
  });
class CategoryEdit extends Component{
    constructor(props){
        super(props);
        let catid = this.props.match.params.id;
        this.state = {
            brand : "",
            brandlist : [],
            group : "",
            name : "",
            description : "",
            color : "",
            cartoon : "",
            pack : "",
            mill : "",
            class : "",
            descriptionweb : "",
            tagweb : "",
            priceweb : "",
            categoryid : catid,
            imageweb : [],
            showOnWeb : "Y",
            checked : true
        }
        
        this.categoryHandle = this.categoryHandle.bind(this);
        this.categoryHandle();

        let token= reactLocalStorage.get('auth');
        let AuthStr = "Bearer ".concat(token);
        axios.get('http://13.229.249.101/dashboard/api/brand/view', { headers: { Authorization: AuthStr } })
        .then(response => {
            const datas = response.data;
            this.setState({
                brandlist : datas
            })
        })
        .catch((error) => {
            console.log('error ' + error);
        });
    }

    categoryHandle = () => {
        let id = this.props.match.params.id;
        let token= reactLocalStorage.get('auth');
        let AuthStr = "Bearer ".concat(token);
        axios.get('http://13.229.249.101/dashboard/api/category/view/'+id, { headers: { Authorization: AuthStr } })
        .then(response => {
            const datas = response.data;
            if(datas.showOnWeb === 'Y'){
                var aktif = true;
            }
            else{
                var aktif = false;
            }
            this.setState({
                brand : datas.brandId,
                group : datas.groupQty,
                name : datas.name,
                description : datas.description,
                color : datas.color,
                cartoon : datas.cartoon,
                pack : datas.pack,
                mill : datas.mill,
                class : datas.class,
                descriptionweb : datas.descriptionWeb,
                tagweb : datas.tagWeb,
                priceweb : datas.priceWeb,
                imageweb : datas.imageWeb,
                showOnWeb : datas.showOnWeb,
                checked : aktif
            })
        })
        .catch((error) => {
            console.log('error ' + error);
        });
    }
    
    handleChange = event => {
        this.setState({ [event.target.name]: event.target.value });
    };

    handleChecked = name => event => {
        this.setState({ [name]: event.target.checked });
    };

    submitHandler = (event) => {
        if(this.state.checked === true){
            var show = 'Y';
        }
        else{
            var show = 'N';
        }
        const payload = {
            "id" : this.state.categoryid,
            "brandId" : this.state.brand,
            "groupQty" : this.state.group,
            "name" : this.state.name,
            "description" : this.state.description,
            "color" : this.state.color,
            "cartoon" : this.state.cartoon,
            "pack" : this.state.pack,
            "mill" : this.state.mill,
            "class" : this.state.class,
            "descriptionWeb" : this.state.descriptionweb,
            "tagWeb" : this.state.tagweb,
            "priceWeb" : this.state.priceweb,
            "showOnWeb" : show
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.put('http://13.229.249.101/dashboard/api/category/edit', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            this.categoryHandle();
        })
        .catch((error) => {
            console.log(error);
        });  
        event.preventDefault();     
    }

    componentDidUpdate(prevProps){
       if(prevProps.match.params.id !== this.props.match.params.id){
            this.categoryHandle();
            let catid = this.props.match.params.id;
            this.setState({
                categoryid : catid
            });   
       }
    }
    handleEditorChange = (e) => {
        this.setState({
            descriptionweb : e.target.getContent()
        }) 
    }

    render(){
        console.log(this.state.descriptionweb);
        const { classes } = this.props;
        const brandlists = this.state.brandlist.map((brand,i) => {
            return <MenuItem key={i} value={brand.id}>{brand.name}</MenuItem>
        });
        const id = this.state.categoryid;
        const descriptionweb = this.state.descriptionweb;
        let page = "category_edit";
        let role = reactLocalStorage.get('role');
        let edit = role.includes(page);

        return(
            edit === false ?<Redirect to="/admin" /> :
            <article className="article">
                <div className="box-default m-3">
                <div className="box-body py-5">
                    <div className="form-group">
                        <h6>Edit Category</h6>
                    </div>
                    <form  onSubmit={this.submitHandler}>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Brand Name</label>
                        <div className="col-md-10">
                        <FormControl className={classes.formControl}>
                         <InputLabel htmlFor="brand-simple">Select Brand</InputLabel>
                            <Select
                                value={this.state.brand}
                                onChange={this.handleChange}
                                inputProps={{
                                    name: 'brand',
                                    id: 'brand-simple',
                                  }}
                                className={classes.selectEmpty}
                            >
                                <MenuItem value="">
                                <em>None</em>
                                </MenuItem>
                                {brandlists}
                            </Select>
                            </FormControl>
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Group</label>
                        <div className="col-md-10">
                        <FormControl className={classes.formControl}>
                         <InputLabel htmlFor="group-simple">Select Group</InputLabel>
                            <Select
                                value={this.state.group}
                                onChange={this.handleChange}
                                inputProps={{
                                    name: 'group',
                                    id: 'group-simple',
                                  }}
                                  className={classes.selectEmpty}
                            >
                                <MenuItem value="">
                                <em>None</em>
                                </MenuItem>
                                <MenuItem value={1}>1 - T-Shirt</MenuItem>
                                <MenuItem value={2}>2 - Sweatshirt</MenuItem>
                                <MenuItem value={3}>3 - No Quantity</MenuItem>
                                <MenuItem value={4}>4 - Caps</MenuItem>
                                <MenuItem value={5}>5 - Heat Transfer</MenuItem>
                                
                            </Select>
                            </FormControl>
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Category Name</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'name'}}
                            onChange={this.handleChange}
                            value={this.state.name}
                            placeholder="Category Name"
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Description</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'description'}}
                            onChange={this.handleChange}
                            value={this.state.description}
                            placeholder="Description"
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Color</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'color'}}
                            onChange={this.handleChange}
                            value={this.state.color}
                            placeholder="Color"
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Cartoon</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'cartoon'}}
                            onChange={this.handleChange}
                            value={this.state.cartoon}
                            placeholder="Cartoon"
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Pack</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'pack'}}
                            onChange={this.handleChange}
                            value={this.state.pack}
                            placeholder="Pack"
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Mill</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'mill'}}
                            onChange={this.handleChange}
                            value={this.state.mill}
                            placeholder="Mill"
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Class</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'class'}}
                            onChange={this.handleChange}
                            value={this.state.class}
                            placeholder="Class"
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Description Website</label>
                        <div className="col-md-10">
                         <Editor
                            value={descriptionweb}
                            apiKey="qlhpachlruoxvvisjzvdcdh0ez0dtofxb2vpal22bqpij1at"
                            initialValue={descriptionweb}
                            init={{
                            plugins: 'link image code',
                            toolbar: 'undo redo | bold italic | alignleft aligncenter alignright | code',
                            height: 400
                            }}
                            onChange={this.handleEditorChange}
                            defaultValue={descriptionweb}
                        />
                       
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Tag Website</label>
                        <div className="col-md-10">
                           <Input
                            inputProps={{name: 'tagweb'}}
                            onChange={this.handleChange}
                            value={this.state.tagweb}
                            placeholder="Tag Website"
                            id="inputName2"
                            multiline
                            fullWidth
                        />
                        
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Price Website</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'priceweb'}}
                            onChange={this.handleChange}
                            value={this.state.priceweb}
                            placeholder="Price Web"
                            id="inputName2"
                            multiline
                            fullWidth
                        />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Show On Web</label>
                        <div className="col-md-10">        
                            <Switch
                            checked={this.state.checked}
                            onChange={this.handleChecked('checked')}
                            value={this.state.showOnWeb}
                            color="primary"
                            />
                        </div>
                    </div>
                    

                    <div className="form-group row">
                        <div className="col-md-2"></div>
                        <div className="col-md-10">
                        <Button variant="contained" className="btn-w-md" type="submit"> Submit </Button>
                        </div>
                    </div>
                    </form>

                </div>
                </div>

                <CategoryUpload idcategory={id} />
                <CategoryColor idcategory={id} />
            </article>
        )
    }
}
CategoryEdit.propTypes = {
    classes: PropTypes.object.isRequired,
  };
export default  withStyles(styles)(CategoryEdit);